import {Professor} from './professor.model';
import {Chair} from './chair.model';
import {CourseTag} from './course-tag.model';
import {CourseSkill} from './course-skill.model';

export class Course {
  id: number;
  name: string;
  type: string;
  ects: number;
  sws: number;
  max_turnout: number;
  professors: Professor[];
  language: string;
  summary: string;
  avg_rating: number;
  avg_difficulty: number;
  avg_usefulness: number;
  percentage_take_again: number;
  percentage_must_attend: number;
  imageUrl: string;
  chair: Chair;
  reviews: any[];
  reviews_count: number;
  top_tags: CourseTag[];
  top_skills: CourseSkill[];
}
