import { Router } from '@angular/router';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { tokenNotExpired } from 'angular2-jwt';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import {throwError} from 'rxjs';
import {AppError} from '../common/errors/app-error';
import {NotFoundError} from '../common/errors/not-found-error';
import {BadRequest} from '../common/errors/bad-request';
import {AuthorizationError} from '../common/errors/authorization-error';
import * as jwt_decode from 'jwt-decode';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private url = 'http://localhost:8000/api';
  constructor(private http: HttpClient, private router: Router) { }

  login(credentials) {
    return this.http.post(this.url + '/login', credentials)
      // .catch((error: HttpErrorResponse) => {
      //   return this.handleError(error);
      // })
      .map((response: any) => response.token);
  }

  logout() {
    if (this.isLoggenIn()) {
      localStorage.removeItem('token');
      this.router.navigate(['/']);
    }
  }

  register(credentials) {
    return this.http.post(this.url + '/register', credentials)
      // .catch((error: HttpErrorResponse) => {
      //   return this.handleError(error);
      // })
      .map((response: any) => response.token);
  }

  getUser(credentials) {
    return this.http.post(this.url + '/profile', credentials)
      // .catch((error: HttpErrorResponse) => {
      //   return this.handleError(error);
      // })
      .map((response: any) => response);
  }

  isLoggenIn() {
    return tokenNotExpired();
  }

  getPayloadData() {
    try {
      return jwt_decode(localStorage.getItem('token'));
    } catch (Error) {
      return null;
    }
  }

  // private handleError(error: HttpErrorResponse) {
  //   if (error.status === 400) {
  //     return throwError(new BadRequest(error));
  //   }

  //   if (error.status === 401) {
  //     return throwError(new AuthorizationError(error));
  //   }

  //   if (error.status === 403) {
  //     return throwError(new NotFoundError(error));
  //   }
  //   return throwError( new AppError(error));
  // }
}
