import { JwtInterceptor } from './common/helpers/jwt-interceptor';
import { ErrorInterceptor } from './common/helpers/error-interceptor';
import { AuthGuard } from './guards/auth.guard';
// import { TokenInterceptor } from './common/helpers/token-interceptor';
import { BrowserModule } from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HomeComponent } from './components/home/home.component';
import {MaterialModule} from './material.module';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {CoursesComponent} from './components/courses/courses.component';
import { CourseComponent } from './components/courses/course/course.component';
import { ProfsComponent } from './components/profs/profs.component';
import { ProfComponent } from './components/profs/prof/prof.component';
import { UniComponent } from './components/uni/uni.component';
import { MenuComponent } from './common/menu/menu.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { RatingComponent } from './common/rating/rating.component';
import { SliderComponent } from './common/slider/slider.component';
import { UserComponent } from './components/user/user.component';
import { CourseReviewComponent } from './components/course-review/course-review.component';
import { DialogComponent } from './common/dialog/dialog.component';
import { DialogOverviewComponent } from './common/dialog-overview/dialog-overview.component';
import { RateComponent } from './common/rate/rate.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import {CourseService} from './services/course.service';
import {AuthService} from './services/auth.service';
import { SignupComponent } from './auth/signup/signup.component';
import { SigninComponent } from './auth/signin/signin.component';
import { NavbarComponent } from './common/navbar/navbar.component';
import { CardComponent } from './common/card/card.component';
import { Top3Component } from './common/top3/top3.component';
import { Top4Component } from './common/top4/top4.component';
import { LongCardComponent } from './common/long-card/long-card.component';
import { FilterPipe} from './common/filter.pipe';
import {PaginatorComponent} from './common/paginator/paginator.component';
import {LoaderComponent} from './common/loader/loader.component';
import {UserProfileComponent} from './components/user-profile/user-profile.component';
import {UserEditProfileComponent} from './components/user-profile/user-edit-profile/user-edit-profile.component';
import {TagsReviewComponent} from './common/tags-review/tags-review.component';
import {AppErrorHandler} from './common/errors/app-error-handler';
import { RateDisplayComponent } from './common/rate-display/rate-display.component';
import {FlashMessagesModule, FlashMessagesService} from 'angular2-flash-messages';
import {BookmarkService} from './services/bookmark.service';
import {ToastService} from 'src/app/services/toast.service';
import {MatAutocompleteModule, MatInputModule, MatSnackBarModule} from '@angular/material';
import { ByProfessorComponent } from './common/filters/by-professor/by-professor.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    CoursesComponent,
    CourseComponent,
    ProfsComponent,
    ProfComponent,
    UniComponent,
    RatingComponent,
    SliderComponent,
    UserComponent,
    CourseReviewComponent,
    DialogComponent,
    DialogOverviewComponent,
    RateComponent,
    MenuComponent,
    SignupComponent,
    SigninComponent,
    NavbarComponent,
    CardComponent,
    Top3Component,
    Top4Component,
    TagsReviewComponent,
    LongCardComponent,
    FilterPipe,
    PaginatorComponent,
    FilterPipe,
    LoaderComponent,
    UserProfileComponent,
    UserEditProfileComponent,
    RateDisplayComponent,
    ByProfessorComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    MaterialModule,
    ReactiveFormsModule,
    MatAutocompleteModule,
    MatSnackBarModule,
    MatInputModule,
    FormsModule,
    NgbModule.forRoot(),
    FlashMessagesModule.forRoot(),
    HttpClientModule
  ],
  providers: [
    CourseService,
    AuthService,
    SigninComponent,
    FlashMessagesService,
    ToastService,
    BookmarkService,
    ToastService,
    // {
    //   provide: HTTP_INTERCEPTORS,
    //   useClass: TokenInterceptor,
    //   multi: true,
    // },
    {
      provide: ErrorHandler,
      useClass: AppErrorHandler
    },
    AuthGuard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: JwtInterceptor,
      multi: true
    },
  ],
  entryComponents: [
    DialogComponent,
    DialogOverviewComponent,
    MenuComponent
  ],
  bootstrap: [AppComponent],
  exports: [DialogComponent, MenuComponent, SigninComponent]
})
export class AppModule { }
