import { Router } from '@angular/router';
import {Component, OnInit, Input, ViewChild, Output, EventEmitter} from '@angular/core';
import {Course} from '../../models/course.model';
import { RatingComponent } from '../rating/rating.component';

@Component({
  selector: 'app-long-card',
  templateUrl: './long-card.component.html',
  styleUrls: ['./long-card.component.scss']
})
export class LongCardComponent implements OnInit {

  @Input() cardImage: string;
  @Input() course: Course;
  @Input() id: number;
  @Input() cardstyle: string;
  @Input() bookmarked: boolean;
  // button to add booksmarks
  @Input() bookmark: boolean;
  @Output() addBookmark = new EventEmitter<number>();
  @ViewChild('rating') rating: RatingComponent;

  // array with images to randomly assign image for a card
  private imgArr = ['assets/images/courses.jpg', 'assets/images/profs.jpg', 'assets/images/course-rate.jpg'];

  constructor(private router: Router) {}

  ngOnInit() {}

  bookmarkCourse(id: number) {
    this.addBookmark.emit(id);
  }

  getRandomImage(): string {
    return this.imgArr[Math.floor(Math.random() * this.imgArr.length)];
  }
}
