import { AuthGuard } from './guards/auth.guard';
import { UserProfileComponent } from './components/user-profile/user-profile.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {CoursesComponent} from './components/courses/courses.component';
import {UniComponent} from './components/uni/uni.component';
import {CourseReviewComponent} from './components/course-review/course-review.component';
import {ProfsComponent} from './components/profs/profs.component';
import {ProfComponent} from './components/profs/prof/prof.component';
import {HomeComponent} from './components/home/home.component';
import {CourseComponent} from './components/courses/course/course.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {SignupComponent} from './auth/signup/signup.component';
import {SigninComponent} from './auth/signin/signin.component';
import {UserComponent} from './components/user/user.component';
import { UserEditProfileComponent } from './components/user-profile/user-edit-profile/user-edit-profile.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'courses',
    component: CoursesComponent
  },
  {
    path: 'courses/:id',
    component: CourseComponent
  },
  {
    path: 'professors',
    component: ProfsComponent
  },
  {
    path: 'professors/:id',
    component: ProfComponent
  },
  {
    path: 'universities',
    component: UniComponent
  },
  {
    path: 'courses/:id/course-review',
    component: CourseReviewComponent
  },
  {
    path: 'signup',
    component: SignupComponent
  },
  {
    path: 'signin',
    component: SigninComponent
  },
  {
    path: 'user',
    component: UserComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'user-edit-profile',
    component: UserEditProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'user-profile',
    component: UserProfileComponent,
    canActivate: [AuthGuard]
  }
  // {path: 'courses/course/courseReview', component: CourseReviewComponent}
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    NgbModule.forRoot(),
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
