import {Component, ElementRef, EventEmitter, OnInit, Output, ViewChild} from '@angular/core';
import {MatAutocompleteSelectedEvent, MatChipInputEvent} from '@angular/material';
import {FormControl} from '@angular/forms';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {QuickSearchService} from '../../../services/quick-search.service';
import {Professor} from '../../../models/professor.model';

@Component({
  selector: 'app-by-professor',
  templateUrl: './by-professor.component.html',
  styleUrls: ['./by-professor.component.scss']
})
export class ByProfessorComponent implements OnInit {
  // mat design vars
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = false;
  separatorKeysCodes: number[] = [ENTER, COMMA];
  professorCtrl = new FormControl();

  // professors vars
  @Output() addProfessor = new EventEmitter<number[]>();
  professors: Professor[] = [];
  professorsHints: any[];
  professorsIds: number[] = [];

  @ViewChild('professorInput') professorInput: ElementRef;

  constructor(private quickSearch: QuickSearchService) {}

  ngOnInit() {
  }

  remove(professorId: number): void {
    console.log('removed');
    const index = this.professorsIds.indexOf(professorId);

    if (index >= 0) {
      this.professorsIds.splice(index, 1);
      this.professors.splice(index, 1);

      this.addProfessor.emit(this.professorsIds);
    }
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    // check if professor is already on the list exists
    if (!this.professorsIds.includes(event.option.value.id)) {
      this.professors.push(event.option.value);
      this.professorsIds.push(event.option.value.id);

      this.addProfessor.emit(this.professorsIds);
    }

    // nullifying variables
    this.professorInput.nativeElement.value = '';
    this.professorCtrl.setValue(null);
  }

  onInputQuickSearch(professor: any): void {
    this.quickSearch.quickSearchProfessor(professor)
      .debounceTime(500)
      .subscribe((hints: any[]) => {
        this.professorsHints = hints;
      });
  }
}
