export class CourseReview {
  user_id: number;
  course_id: number;
  star_rating: number;
  user_major: string;
  user_gpa: number;
  user_course_grade: number;
  interesting: number;
  difficulty: number;
  usefulness: number;
  must_attend: number;
  often_study: number;
  take_again: number;
  pay_attention_in_class: number;
  script: number;
  clearness: number;
  completeness: number;
  bullshitmeter: number;
  relevance: number;
  attend_seminars: number;
  memorize_script: number;
  do_extra_work: number;
  study_old_exams: number;
  take_notes: number;

  constructor(userId: number, courseId: number) {
    this.user_id = userId;
    this.course_id = courseId;
  }
}
