import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class QuickSearchService {
  private urlCourses = 'http://localhost:8000/api/courses/quick-search';
  private urlProfessors = 'http://localhost:8000/api/professors/quick-search';
  private urlChairs = 'http://localhost:8000/api/cgairs/quick-search';
  private urlSkills = 'http://localhost:8000/api/skills/quick-search';

  constructor(private http: HttpClient) { }

  quickSearchCourse(term: string) {
    return this.http.get(this.urlCourses, {
      params: {q: term}
    });
  }

  quickSearchProfessor(term: string) {
    return this.http.get(this.urlProfessors, {
      params: {q: term}
    });
  }

  quickSearchSkills(term: string) {
    return this.http.get(this.urlSkills, {
      params: {q: term}
    });
  }

}
