export class CourseQuery {
  q: string;
  star_rating: number;
  professor: string;
  sort_name: string;

  constructor(q?: string, star_rating?: number, professors?: string, sort_name?: string) {
    if (q) {
      this.q = q;
    }
    if (star_rating) {
      this.star_rating = star_rating;
    }
    if (professors) {
      this.professor = professors;
    }
    if (sort_name) {
      this.sort_name = sort_name;
    }
  }
}
