import {Component, OnInit, ViewChild, ViewChildren, QueryList, Output, EventEmitter} from '@angular/core';
import {Course} from '../../models/course.model';
import {CourseService} from '../../services/course.service';
import {LongCardComponent} from '../../common/long-card/long-card.component';
import 'rxjs/add/operator/map';
import {LoaderComponent} from '../../common/loader/loader.component';
import {BookmarkService} from '../../services/bookmark.service';
import {CourseQuery} from '../../models/course-query';
import {StarRating} from '../../common/enums/star-rating';
import {Paginator} from '../../models/paginator';
import {SearchControlService} from '../../services/search-control.service';
import {ToastService} from '../../services/toast.service';
import {AuthService} from '../../services/auth.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html',
  styleUrls: ['./courses.component.scss'],
  providers: [CourseService]
})
export class CoursesComponent implements OnInit {

  @ViewChildren(LongCardComponent) longCardComponents: QueryList<LongCardComponent>;
  @ViewChild(LoaderComponent) loader;
  courses: Course[];

  // for search and filter queries
  query: CourseQuery = new CourseQuery();

  // rating enum
  StarRating: typeof StarRating = StarRating;

  // page paginate properties
  paginator: Paginator;

  // array with images to randomly assign image for a card
  private imgArr = ['assets/images/courses.jpg', 'assets/images/profs.jpg', 'assets/images/course-rate.jpg'];

  // component visual properties
  isItemVisible = true;
  loading = false;
  cardStyle = 'rounded';

  constructor(
    private authService: AuthService,
    private courseService: CourseService,
    private boomarkService: BookmarkService,
    private searchControlService: SearchControlService,
    private toastService: ToastService,
    private router: ActivatedRoute) {}

  ngOnInit() {
    // TODO: check subscription

    this.loadCourses(null, null);

    this.searchControlService.getSearchQuery()
      .subscribe(searchable => {
        console.log(searchable);
        this.query.q = searchable;
        this.loadCourses(null, null);
      });
  }

  loadCourses(page?: number, pageUrl?: string): void {
    this.loading = true;

    if (page) {
      this.courseService.getAllByPageNumber(page, this.query)
        .map((response: any) => {
          return this.mapResponse(response);
        })
        .subscribe((courses: Course[]) => {
          this.courses = courses;
        });
    } else if (pageUrl) {
      this.courseService.getAllByPageUrl(pageUrl, this.query)
        .map((response: any) => {
          return this.mapResponse(response);
        })
        .subscribe((courses: Course[]) => {
          this.courses = courses;
        });
    } else {
      console.log(this.query);
      this.courseService.getAll(this.query)
        .map((response: any) => {
          return this.mapResponse(response);
        })
        .subscribe((courses: Course[]) => {
          this.courses = courses;
        });
    }
  }

  goToPage(page: number): void {
    this.loadCourses(page, null);
  }

  toNext(): void {
      this.loadCourses(null, this.paginator.next_page_url);
  }

  toPrev(): void {
      this.loadCourses(null, this.paginator.prev_page_url);
  }

  sortAsc(): void {
    this.query.sort_name = 'asc';
    this.loadCourses(this.paginator.current_page, null);
  }

  sortDesc(): void {
    this.query.sort_name = 'desc';
    this.loadCourses(this.paginator.current_page, null);
  }

  applyProfessorsFilter(professorsIds: number[]): void {
    this.query.professor = JSON.stringify(professorsIds);
    this.loadCourses(null, null);
    console.log(professorsIds);
  }

  addBookmark(id: number): void {
    this.boomarkService.add(id)
      .subscribe((message: string) => {
        this.toastService.showToaster(message);
      });
  }

  private mapResponse(response: any): Course[] {
    this.paginator = response;

    for (const course of response.data) {
      course.imageUrl = this.imgArr[Math.floor(Math.random() * this.imgArr.length)];
      course.avg_rating = course.avg_rating.length > 0 ? course.avg_rating[0].average : 0;
    }

    this.loading = false;

    return response.data;
  }
}
