import { UserService } from './../../services/user.service';
import {Component, OnInit, TemplateRef, ViewChild, ViewEncapsulation, Injectable, Output, EventEmitter} from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {NgbModal, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';
import {Router} from '@angular/router';
import {AppError} from '../../common/errors/app-error';
import {interval} from 'rxjs';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./signin.component.scss']
})

@Injectable()
export class SigninComponent implements OnInit {
  @ViewChild('signIn')
  private signInRef: TemplateRef<any>;
  public modalReference: NgbModalRef;

  @ViewChild('loginContinue')
  private loginContinue: TemplateRef<any>;

  @Output() callSignUp = new EventEmitter<boolean>();

  invalidLogin: boolean;

  constructor(
      private modalService: NgbModal,
      private authService: AuthService,
      private userService: UserService,
      private router: Router) {}

  ngOnInit() {}

  loadSignIn() {
    this.signIn(this.signInRef);
  }

  signIn(content) {
    this.modalReference = this.modalService.open(content,
      {backdropClass: 'light-blue-backdrop', centered: true});
  }

  loginSignUp($event) {
    this.modalReference.close();

    // open sign up form
    this.callSignUp.emit($event);
  }

  emailLogin(credentials) {
    this.authService.login(credentials)
        .subscribe(token => {
          if (token) {
              // console.log(token);
              localStorage.setItem('token', token);
              this.modalReference.close();
              this.router.navigate(['user-profile']);
              this.userService.getUserInfo();
          }
        }, (error) => {
          console.log(error);
          this.invalidLogin = true;

          // showing invalid login error for 2 seconds
          setInterval(() => {
            this.invalidLogin = false;
          }, 2000);
        });
  }

}
