import {Component, Input, OnInit} from '@angular/core';
import {CardComponent} from '../card/card.component';
import {CourseComponent} from '../../components/courses/course/course.component';
import {Course} from '../../models/course.model';
import {CourseService} from '../../services/course.service';

@Component({
  selector: 'app-top4',
  templateUrl: './top4.component.html',
  styleUrls: ['./top4.component.scss']
})
export class Top4Component implements OnInit {
  @Input() resourceImageUrl: string;
  @Input() showResourceImage: boolean;
  @Input() resourceImagePositionRight: boolean;
  items: any[];

  constructor(private service: CourseService) { }

  ngOnInit() {
    this.items = this.service.getTop4();
  }

}

