import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from './../../../services/user.service';
import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

@Component({
  selector: 'app-user-edit-profile',
  templateUrl: './user-edit-profile.component.html',
  styleUrls: ['./user-edit-profile.component.scss']
})
export class UserEditProfileComponent implements OnInit {

  graduationDate = new Date();
  birthDate = new Date();
  imagePath = '';
  @ViewChild('pictures')
  private picturesRef: TemplateRef<any>;
  public modalReference: NgbModalRef;

  constructor(private userService: UserService,
    private modalService: NgbModal) { }

  ngOnInit() {
    this.imagePath = this.userService.getCurrentUserImage();
  }

  loadPictures() {
    this.openPictures(this.picturesRef);
  }

  openPictures(content) {
    this.modalReference = this.modalService.open(content,
      {backdropClass: 'light-blue-backdrop', centered: true});
  }

}
