
import { UserService } from './../../services/user.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import {SigninComponent} from '../../auth/signin/signin.component';
import {SignupComponent} from '../../auth/signup/signup.component';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';
import 'rxjs/add/operator/debounceTime';
import {SearchControlService} from '../../services/search-control.service';
import {QuickSearchService} from '../../services/quick-search.service';
import {User} from '../../models/user.model';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})

export class NavbarComponent implements OnInit {
  currentUser: User;
  @ViewChild(SigninComponent) signIn: SigninComponent;
  @ViewChild(SignupComponent) signUp: SignupComponent;
  public searchable: string;
  public searchHints: any;

  constructor(
    public authService: AuthService,
    private router: Router,
    private userService: UserService,
    private searchControlService: SearchControlService,
    private quickSearch: QuickSearchService) {}

  ngOnInit() {}

  getUserName(): string {
    return this.userService.getCurrentUserName();
  }

  getUserImage(): string {
    return this.userService.getCurrentUserImage();
  }

  loadSignInComponent() {
    this.signIn.loadSignIn();
  }

  loadSignUpComponent() {
    this.signUp.loadSignUp();
  }

  logout() {
      this.authService.logout();
  }

  onInputSearch() {
    this.quickSearch.quickSearchCourse(this.searchable)
      .debounceTime(500)
      .subscribe(data => {
        this.searchHints = data;
      });
  }

  searchCourse() {
    this.searchControlService.setSearchQuery(this.searchable);
    this.router.navigate(['courses']);
  }
}
