import { Course } from 'src/app/models/course.model';

export class User {
  id: number;
  name: string;
  email: string;
  sex: number;
  study_program: string;
  location?: string;
  image: string;
  birth_date?: Date;
  created_at: Date;
  updated_at: Date;
  bookmarks?: Course[];
  reviewed_courses?: Course[];

  constructor(program: string, image: string) {
    this.study_program = program;
    this.image = image;
  }
}
