import {Component, OnInit, TemplateRef, ViewChild, ViewEncapsulation, Injectable, Output, EventEmitter} from '@angular/core';
import {NgbModal, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';
import {NgForm} from '@angular/forms';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';
import {AppError} from '../../common/errors/app-error';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./signup.component.scss']
})
@Injectable()
export class SignupComponent implements OnInit {

  // Bootstrap modal

  closeResult: string;
  @ViewChild('signUp')
  private signUpRef: TemplateRef<any>;
  public modalReference: NgbModalRef;

  @Output() callSignIn = new EventEmitter<boolean>();

  differentPasswords: boolean;

  constructor(
    private modalService: NgbModal,
    private authService: AuthService,
    private router: Router) {}

  ngOnInit() {}

  loadSignUp() {
    this.signUp(this.signUpRef);
  }

  signUp(content) {
    this.modalReference = this.modalService.open(content,
      {backdropClass: 'light-blue-backdrop', centered: true});
  }

  signUpLogin($event) {
    this.modalReference.close();

    // open sign up form
    this.callSignIn.emit($event);
  }

  signUpAndLogin(credentials) {
    console.log(credentials);

    this.authService.register(credentials)
      .subscribe(token => {
        if (token) {
          localStorage.setItem('token', token);
          this.modalReference.close();
          this.router.navigate(['user-profile']);
        }
      }, (error: AppError) => {
        console.log(error);
        this.differentPasswords = true;
      });
  }

  // End Bootstrap modal

  // onSignUp(form: NgForm) {
  //   const email = form.value.email;
  //   const password = form.value.password;
  // }

}
