import {Course} from '../../../models/course.model';
import {Component, OnInit} from '@angular/core';
import {CourseReview} from '../../../models/course-review.model';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {DialogComponent} from '../../../common/dialog/dialog.component';
import {CourseService} from '../../../services/course.service';
import {CourseTagsReview} from '../../../models/course-tags-review';
import {Observable} from 'rxjs';
import 'rxjs/add/observable/combineLatest';
import {BookmarkService} from '../../../services/bookmark.service';
import {ToastService} from '../../../services/toast.service';

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.scss'],
  providers: [DialogComponent]
})

export class CourseComponent implements OnInit {
  review: CourseReview;
  course: Course;
  id: number;
  // isReviewVisible = false;
  isReviewVisible = true;
  isMessageVisible = false;
  loading = false;
  countLoad = 0;
  disabled = false;

  constructor(private courseService: CourseService,
              private router: Router,
              private route: ActivatedRoute,
              public dialog: DialogComponent,
              private toastService: ToastService) { }

  ngOnInit() {
    this.loading = true;
    this.isReviewVisible = true;
    this.loadCourse();
  }

  loadCourse() {
      this.route.params.subscribe(
          (params: Params) => {
              this.id = +params['id'];
              console.warn('ID: ', this.id);
              this.courseService.getOne(this.id)
                  .map((response: any) => {
                      this.course = response;
                      console.warn('COURSE: ', response);
                      this.id = response.id;
                      this.course.imageUrl = response.imageUrl;
                      this.course.imageUrl = 'assets/images/courses.jpg'; // tmp
                      this.course.avg_rating = response.avg_rating.length > 0 ? response.avg_rating[0].average : 0;
                      this.course.avg_difficulty = response.avg_difficulty.length > 0 ? response.avg_difficulty[0].average : 0;
                      // this.avg_difficulty = this.course.avg_difficulty;
                      this.course.avg_usefulness = response.avg_usefulness.length > 0 ? response.avg_usefulness[0].average : 0;
                      this.course.percentage_must_attend = response.percentage_must_attend.length > 0 ? response.percentage_must_attend[0].percentage : 0;
                      this.course.percentage_take_again = response.percentage_take_again.length > 0 ? response.percentage_take_again[0].percentage : 0;
                      this.course.top_tags = response.top_tags;
                      this.course.top_skills = response.top_skills;
                      this.course.reviews_count = this.course.reviews.length;
                      return response;
                  })
                  .subscribe((data: Course) => {
                      console.warn('Course: ', data);
                      this.checkLoad();
                  });
          }
      );
  }

  checkLoad() {
    this.countLoad++;
    if (this.countLoad === 1) {
      this.loading = false;
    }
  }

  reviewCourseStart() {
    this.review = new CourseReview(3, this.id);
    // this.isReviewVisible = true;
    // this.dialog.openDialog();
  }

  // reviewCourseSubmit() {
  //   //  this.isReviewVisible = false;
  //   this.isMessageVisible = true;
  //   this.isReviewVisible = true;
  // }

  reviewCourseSubmit(tagsReview: CourseTagsReview) {
    Observable.combineLatest([
      this.courseService.submitStarRating(this.id, tagsReview.star_rating),
      this.courseService.attachSkills(this.id, tagsReview.skills),
      this.courseService.attachTags(this.id, tagsReview.star_rating, tagsReview.tags)
    ]).subscribe((combined: any) => {
      console.log(combined);
      this.course.avg_rating = combined[0].avg_rating.length > 0 ? combined[0].avg_rating[0].average : 0;
      this.course.top_skills = combined[1].top_skills;
      this.course.top_tags = combined[2].top_tags;
      this.toastService.showToaster('Your review has been successfully submitted');
      this.isReviewVisible = false;
      // this.changeMessageVisibility();
    });
  }

  goToFullCourseReview() {
    this.router.navigate(['courses', this.id, 'course-review']);
  }

  // TODO: relative position of the success message. It overlaps the review box
  changeMessageVisibility(): void {
    this.isMessageVisible = true;
    setInterval(() => {
      this.isMessageVisible = false;
    }, 3000);
  }
}
