
import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {MatAutocompleteSelectedEvent, MatChipInputEvent} from '@angular/material';
import {CourseReview} from '../../models/course-review.model';
import {CourseService} from '../../services/course.service';
import {QuickSearchService} from '../../services/quick-search.service';
import {CourseSkill} from '../../models/course-skill.model';
import {FormControl} from '@angular/forms';
import {CourseTagsReview} from '../../models/course-tags-review';

@Component({
  selector: 'app-tags-review',
  templateUrl: './tags-review.component.html',
  styleUrls: ['./tags-review.component.scss']
})
export class TagsReviewComponent implements OnInit {

  // chips configuration
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  skillControl = new FormControl();

  // skills configuration
  showSkills: boolean;

  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  @Input() review: CourseReview;
  @ViewChild('skillInput') skillInput: ElementRef;
  @Output() tagsReviewSubmit = new EventEmitter<CourseTagsReview>();

  tags: any[];
  skills: CourseSkill[] = [];
  skillsHints: any[];
  tagsReview: CourseTagsReview = new CourseTagsReview([], [], 0);

  constructor(
    private courseService: CourseService,
    private quickSearchService: QuickSearchService) { }

  ngOnInit() {
    this.showSkills = true;
  }

  onRateSet(): void {
    this.tags = this.courseService.getTagsByRating(this.tagsReview.star_rating);
    this.tagsReview.tags = [];
  }

  changeSelected(tag): void {
    tag.state = !tag.state;
    if (tag.state === true) {
      this.tagsReview.tags.push(tag.name);
    } else {
      const index = this.tagsReview.tags.indexOf(tag.name);
      if (index > -1) {
        this.tagsReview.tags.splice(index, 1);
      }
    }
  }

  onInputQuickSearch(skill: any): void {
    this.quickSearchService.quickSearchSkills(skill)
      .debounceTime(500)
      .subscribe((hints: any[]) => {
        this.skillsHints = hints;
      });
  }

  // if skill was selected from the autocomplete list
  selected(event: MatAutocompleteSelectedEvent): void {
    // check if skill is already in the list
    if (!this.tagsReview.skills.includes(event.option.value.name)) {
      this.skills.push(event.option.value);
      this.tagsReview.skills.push(event.option.value.name);
    }

    // nullifying variables
    this.skillInput.nativeElement.value = '';
    this.skillControl.setValue(null);
  }

  // TODO: if manual input for skills was used
  addSkill(event: MatChipInputEvent): void {
    const input = event.input;
    const skillName = event.value;

    // trim skill
    if ((skillName || '').trim()) {
      if (!this.tagsReview.skills.includes(skillName)) {
        const skill = new CourseSkill();
        skill.name = skillName.trim();
        this.skills.push(skill);
        this.tagsReview.tags.push(skillName.trim());
      }
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }
    this.skillControl.setValue(null);
  }

  removeSkill(skill: CourseSkill): void {
    const index = this.tagsReview.skills.indexOf(skill.name);

    if (index >= 0) {
      console.log('exists');
      this.tagsReview.skills.splice(index, 1);
      this.skills.splice(index, 1);
    }
  }

  submitTagsReview(): void {
    this.tagsReviewSubmit.emit(this.tagsReview);
  }
}
