import { Course } from './../../models/course.model';
import { User } from './../../models/user.model';
import { UserService } from './../../services/user.service';
import { CourseService } from '../../services/course.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {
  // imagePath = 'assets/images/profile-tmp.png';
  // imagePath = 'assets/images/users-pics/ch-w.png';
  imagePath: string;
  currentUser: User;
  // cardStyle = 'edgy';
  cardStyle = 'rounded';
  // loading = true;
  isFirstTabActive = true;
  isSecondTabActive = false;
  reviewsCount = 0;
  // countLoad = 0;

  loading = false;
  countLoad = 0;

  constructor(private webService: CourseService, private userService: UserService) {
    this.currentUser = new User('not defined', this.imagePath);
    this.imagePath = this.userService.getCurrentUserImage();
   }

  ngOnInit() {
    this.loading = true;
    this.getUserInfo();
  }

  getUserInfo() {
    this.userService.getCurrentUser()
      .subscribe((data: User) => {
        this.loading = this.userService.loading;
        this.currentUser = data;
        if (this.currentUser.bookmarks.length > 0) {
          this.currentUser.bookmarks.forEach(bookmark => {
            bookmark.avg_rating = this.getBookmarkedCourseRating(bookmark);
          });
        }
        // console.log('USER: ', this.currentUser);
        // console.log('Bookmarks: ', this.currentUser.bookmarks);
      });
  }

  getBookmarkedCourseRating(course: any): number {
    let rating = 0;
    if (course.avg_rating.length > 0) {
      rating = course.avg_rating[0].average;
    }
    return rating;
  }

  acivateFirstTab() {
    this.isFirstTabActive = true;
    this.isSecondTabActive = false;
  }

  acivateSecondTab() {
    this.isSecondTabActive = true;
    this.isFirstTabActive = false;
  }

//   checkLoad() {
//     this.countLoad++;
//     if (this.countLoad === 1) {
//       this.loading = false;
//     }
// }
}
